<template>
	<div>
		<headerNav title="尚未授权" :showBackArrow="false"/>

		<div class="logo">
			<img src="../../../static/img/home/authFail.png"/>
		</div>

		<div class="tip">您的账号暂未审核通过</div>

		<div class="tip-txt">请至电脑端完善资料并等待总部审核</div>

		<div style="display: flex; justify-content: center; margin-top: 30px;">
			<Button type="error" size="large" @click="signOutFunc" >切换账号</Button>
		</div>
	</div>
</template>

<script>
	import headerNav from '../../components/header/nav'
	import {mapActions} from 'vuex';

	export default {
		name: 'Auth',

		components: {
			headerNav,
		},

		data() {
			return {}
		},

		methods: {
			...mapActions(['signOut']),

			signOutFunc() {
				this.signOut();
				this.$router.replace('/login');
			},
		},
	}
</script>

<style>
	.logo {
		width: 100%;
		margin: 10% 0;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.logo img {
		width: 60%;
		height: auto;
	}

	.tip {
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 14px;
	}

	.tip-txt {
		margin-top: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 12px;
		color: #999;
		text-align: center;
		line-height: 26px;
	}
</style>



